import React from "react";
import "./Footer.css";
import { NavList, Button } from "./../../components";
import classNames from "classnames";
import PropTypes from "prop-types";

const Footer = ({ userId, editMode, readOnlyMode }) => {
    const getClasses = classNames({
        "c-footer__buttons": true,
        "is-active": editMode,
        "is-disabled": readOnlyMode
    });
    const links = [
        {
            name: "Rewards",
            to: `/app/${userId}/rewards`,
            icon: "hamburger"
        },
        {
            name: "Favourites",
            to: `/app/${userId}/favourites`,
            icon: "hamburger"
        },
        {
            name: "Search",
            to: `/app/${userId}/search`,
            icon: "symbol"
        },
        {
            name: "Groups",
            to: `/app/${userId}/groups`,
            icon: "close"
        },
        {
            name: "Settings",
            to: `/app/${userId}/settings`,
            icon: "cogs"
        }
    ];
    const editButtons = [
        {
            text: "Archive"
        },
        {
            text: "Delete"
        }
    ];

    return (
        <div className="l-footer">
            <NavList arr={links} userId={userId} isSideBarVisible={editMode} />
            <div className={getClasses}>
                {editButtons.map((button, index) => (
                    <Button key={index} text={button.text} size="narrow" />
                ))}
            </div>
        </div>
    );
};

Footer.propTypes = {
    userId: PropTypes.number,
    groupId: PropTypes.number,
    hasLinks: PropTypes.bool
};

export default Footer;



